.App {
  text-align: center;
}

.chat-title {
  font-size: 1.9em;
  margin-left: 5%;
}

.main {
  margin: 50px;
}

.messages-block {  
  text-align: left;
  max-width: 1000px;
  margin: 0 auto;
  margin-top: 50px;
  overflow-y: auto;
  height: 600px;
  border: 1px solid red;
  padding: 10px;
}

.nickname {
  margin-left: 15px;
}

.message {
  margin-bottom: 10px;
}

.message-text {
  background-color: rgb(154, 212, 235);
  width: 100%;
  padding: 3px 20px;
  font-size: 1.1em;
  display: block;
  border-radius: 15px;
}

.time {
  font-size: 0.8em;
  float: right;
}
